<script>
	export let cand
	export let index
	export let undo
	export let op
	$: a = cand[index].a
	$: b = cand[index].b
	$: done = a==b
	$: hist = cand[index].hist
</script>

<style>
	.fs {font-size:1.5em}
	.marg {margin: 0px}
	.br {border-radius:5px}
</style>

<div class="row">

	{#if done}
		<div class="left col s12 fs marg center-align">{hist.length} steps</div>
	{:else}
		<div class="left col s3 fs marg center-align"></div>
		<div class="left col s6 fs marg center-align">
			<button class=br on:click={() => op(a+2)} disabled={done}>+2</button>
			<button class=br on:click={() => op(a*2)} disabled={done}>*2</button>
			<button class=br on:click={() => op(a/2)} disabled={done || a%2==1}>/2</button>
		</div>
		<div class="left col s3 fs marg right-align">
			<button on:click={undo} disabled={hist.length==0}>Undo</button>
		</div>
	{/if}
</div>
