<script>
	export let index
	export let cand
	$: curr = cand[index]
</script>

<style>
	.fs {font-size:1.5em}
</style>

<div class='col s3 fs left-align' >
	<button on:click={() => index--} disabled={index==0}>Prev</button>
</div>

<div class='col s6 fs center-align' >
	{#if curr.a==curr.b}
		{curr.orig} to {curr.b}
	{:else}
		{curr.a} to {curr.b}
	{/if}
</div>

<div class='col s3 fs right-align' >
	<button on:click={() => index++} disabled={index==23}>Next</button>
</div>
